.itemCreator {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 100;
}

.todoItem {
    padding: 5px;
    margin: 1em;
    float: left;
    list-style: none;
}

.todoItem div {
    text-decoration: none;
    font-family: 'Nothing You Could Do', cursive;
    color: #000;
    background-color: #fff;
    display: block;
    height: 10em;
    width: 10em;
    padding: 1em;
    box-shadow: 5px 5px 7px rgba(33,33,33,.7);
    transition: -webkit-transform .15s linear;
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
    overflow: hidden;
}

.itemCreator div {
    box-shadow: -5px 5px 7px rgba(33,33,33,.7);
}

.itemCreator div textarea {
    resize: none;
}

.todoItem.complete div p {
    text-decoration: line-through;
}

.todoItem.complete div p:before {
    position: absolute;
    content: '';
    background: #000;
    display: block;
    width: 100%;
    height: 1px;
    -webkit-transform: rotate(-50deg);
            transform: rotate(-50deg);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.todoItem.complete div p:after {
    position: absolute;
    content: '';
    background: #000;
    display: block;
    width: 100%;
    height: 1px;
    -webkit-transform: rotate(50deg);
            transform: rotate(50deg);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.todoItem:nth-child(even) div {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
    position: relative;
    top: 5px;
}

.todoItem:nth-child(3n) div {
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
    position: relative;
    top: -5px;
}

.todoItem:nth-child(4n) div {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
    position: relative;
    top: -5px;
}

.todoItem:nth-child(5n) div {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
    position: relative;
    top: -10px;
}

.todoItem div:hover, .todoItem div:focus {
    box-shadow: 10px 10px 7px rgba(0,0,0,.7);
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    position: relative;
    z-index: 5;
}

.todoItem div .icon {
    display: block;
    position: absolute;
    visibility: hidden;
    background: transparent;
}

.todoItem div:hover .icon {
    visibility: visible;
}

.todoItem div .icon {
    color: #000;
    background: transparent;
}

.todoItem div .icono-trash,
.itemCreator div .icono-plusCircle {
    bottom: 2px;
    right: 2px;
}

.todoItem div .icono-check,
.todoItem div .icono-cross {
    border: 0;
    bottom: 2px;
    left: 2px;
}

.todoItem.complete div .icono-check {
    display: none;
}

.todoItem.todo div .icono-cross {
    display: none;
}

.itemsList-ul {
  list-style: none;
  margin-right: 100px;
}

.itemsList-ul {
  overflow: hidden;
  padding: 3em;
}

.app {
  text-align: center;
}

.app-intro {
  font-size: large;
}

* {
  margin:0;
  padding:0;
}
body {
  font-family:arial,sans-serif;
  font-size:100%;
  margin:1em;
  background:#666;
  color:#fff;
}
h2, p {
  font-size:100%;
  font-weight:normal;
}

